import React from "react";
import Note from "./Note";
import Measure from "./Measure";
import LaneLine from "./LaneLine";

function ChartDetail(props: any) {
    const [loadedChart, setLoadedChart] = React.useState<any[]>([]);
    const [loadedMeasure, setLoadedMeasure] = React.useState<any[]>([]);
    const [noteHeight, setNoteHeight] = React.useState(24);
    const [lastMeasure, setLastMeasure] = React.useState(0);
    const [isLoading, setIsLoading] = React.useState(true);

    React.useEffect(() => {
        let tmpLast = 0;
        setIsLoading(true)
        fetch(process.env.PUBLIC_URL + '/charts/' + props.id.toLowerCase() + '.json')
            .then(response => response.json())
            .then(data => {
                data.forEach((obj: { showTime: boolean; }) => {
                    obj.showTime = false;
                });
                setLoadedChart(data);
                tmpLast = data[data.length - 1].JudgeTime + 0.1;
            })
            .catch(error => console.error(error));
        fetch(process.env.PUBLIC_URL + '/charts/' + props.id.toLowerCase() + '_measures.json')
            .then(response => response.json())
            .then(data => {
                if (data[data.length - 1] + 0.1 > tmpLast) {
                    tmpLast = data[data.length - 1] + 0.1;
                }
                setLastMeasure(tmpLast);
                setNoteHeight((data[1] - data[0]) * 8);
                setLoadedMeasure(data);
            })
            .then(
                ()=>setIsLoading(false)
            )
            .catch(error => console.error(error));
    }, [props.id]);

    if (isLoading) {
        return (<div className="flex w-full justify-center my-10 space-x-2"><svg id="loading-spinner" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 48 48">
            <defs>
                <linearGradient id="spinner-gradient-a" x1="49.892%" x2="55.03%" y1="58.241%" y2="89.889%">
                    <stop offset="0%" />
                    <stop offset="22.44%" stopOpacity=".59" />
                    <stop offset="100%" stopOpacity="0" />
                </linearGradient>
            </defs>
            <g fill="none" transform="translate(-8 -8)">
                <path d="M32,56 C18.745166,56 8,45.254834 8,32 C8,18.745166 18.745166,8 32,8 C45.254834,8 56,18.745166 56,32 C56,45.254834 45.254834,56 32,56 Z M32,52 C43.045695,52 52,43.045695 52,32 C52,20.954305 43.045695,12 32,12 C20.954305,12 12,20.954305 12,32 C12,43.045695 20.954305,52 32,52 Z" />
                <path fill="url(#spinner-gradient-a)" d="M56,32 C56,33.1045695 55.1045695,34 54,34 C52.8954305,34 52,33.1045695 52,32 C52,20.954305 43.045695,12 32,12 C20.954305,12 12,20.954305 12,32 C12,43.045695 20.954305,52 32,52 C33.1045695,52 34,52.8954305 34,54 C34,55.1045695 33.1045695,56 32,56 C18.745166,56 8,45.254834 8,32 C8,18.745166 18.745166,8 32,8 C45.254834,8 56,18.745166 56,32 Z" transform="rotate(45 32 32)" />
            </g>
        </svg><span>Loading Chart</span></div>)
    }
    else {
        return (
            <div className='relative mx-4' style={{ height: lastMeasure * 128 + 'px' }}>
                <LaneLine max={lastMeasure} />
                {
                    loadedMeasure.map(
                        (measure, index) => {
                            return <Measure measureCount={index + 1}
                                key={'measure' + index}
                                time={measure}
                                max={lastMeasure} />
                        }
                    )
                }
                {
                    loadedChart.map((note, index) => {
                        return <Note
                            key={'note' + index}
                            height={noteHeight}
                            noteProp={note}
                            max={lastMeasure}
                            count={index}
                            color={props.colors[note.CastIndex]} />
                    }
                    )
                }
            </div>)
    };
}

export default ChartDetail;