function LaneLine(props: any) {
    const heightStyle={
        height: (props.max-0.1)* 128+'px',
        left:  '32px',
        top: '12.8px'
    }
    return(
        <div style={heightStyle} className='absolute flex'>
            <div className="border-r border-gray-200 w-8"></div>
            <div className="border-r border-gray-200 w-8"></div>
            <div className="border-r border-gray-200 w-8"></div>
            <div className="border-r border-gray-200 w-8"></div>
        </div>
    )
}

export default LaneLine;