import ChartHeader from '../components/ChartHeader';
import ChartDetail from '../components/ChartDetail';
import {useLocation} from "react-router-dom";
import SongTitles from '../components/SongTitles.json';
import backSvg from '../back.svg';
import {Link} from 'react-router-dom';
import CastSetting from '../components/CastSetting';
import { useState } from 'react';

function Chart() {
    const search = useLocation().search;
    const chartID: string  = new URLSearchParams(search).get('id') ?? "m1_w1";
    const chartLevel = new URLSearchParams(search).get('level');
    const [chartColors, setChartColors] = useState(["rgb(255, 153, 200)","rgb(252, 246, 189)","rgb(208, 244, 222)","rgb(169, 222, 249)","rgb(228, 193, 249)",]);

    return (
        <div>
            <div className='fixed top-2 left-2 h-8 w-8 z-50'>
                <Link to='/'>
                    <img src={backSvg} height='2rem' alt='Back to chart selection'/></Link>
            </div>
            <div className='max-w-lg m-auto bg-white'>
            <ChartHeader id={chartID} name={SongTitles[chartID as keyof typeof SongTitles]} level={chartLevel}/>
            <CastSetting colors={chartColors} setChartColors={setChartColors}/>
            <ChartDetail id={chartID+'_'+chartLevel} colors={chartColors}/>
        </div></div>)
}

export default Chart;
