function Measure(props: any) {
    return (
            <div className='absolute w-full border-t border-gray-400 text-right text-gray-500'
            style={{ top: (props.max - props.time) * 128 + 'px' }}>
                {props.measureCount} / {props.time.toFixed(2)}s
            </div>)
}

export default Measure;