// import arrow from '../arrow.svg'

function ChartHeader(props: any) {
    return (
        <div className='flex items-end p-4 space-x-10 justify-between w-full'>
            <div className='flex items-end space-x-5'>
                <div className="text-lg">{props.name}</div>
                <div className='flex items-end'><img src={process.env.PUBLIC_URL+"/images/jacket/jacket-" + props.id + '.png'} width="150px" alt={props.name}/></div>
                {props.level==='1'&&<div>Normal</div>}
                {props.level==='2'&&<div>Hard</div>}
                {props.level==='3'&&<div>Advanced</div>}
                {/* <div>Best Estimated:</div> */}
            </div>
            {/* <div><img src={arrow} alt='Show/Hide Team Settings' width="20px"/></div> */}
        </div>)
}

export default ChartHeader;