function Note(props: any) {
    const singleStyle = {
        width: props.noteProp.Size * 32 + 'px',
        height: '10px',
        backgroundColor: props.color
    }
    const lineStyle = {
        width: props.noteProp.Size * 32 - 8 + 'px',
        height: props.height,
        backgroundColor: 'rgba'+props.color.slice(3,-1)+', 0.5)'
    }
    const endStyle = {
        width: props.noteProp.Size * 32 - 8 + 'px',
        height: props.height,
        backgroundColor: 'rgba'+props.color.slice(3,-1)+', 0.5)'
    }
    const singleWrapper = {
        top: (props.max - props.noteProp.JudgeTime) * 128 - 5 + 'px',
        left: props.noteProp.LanePosition * 32 + 32 + 'px'
    }
    const lineWrapper = {
        top: (props.max - props.noteProp.JudgeTime) * 128 + 'px',
        left: props.noteProp.LanePosition * 32 + 36 + 'px'
    }
    const endWrapper = {
        top: (props.max - props.noteProp.JudgeTime) * 128 + 'px',
        left: props.noteProp.LanePosition * 32 + 36 + 'px'
    }

    const noteInform = <div className='hiddenChild z-20 stroke ml-5'>
        {props.count} / {props.noteProp.JudgeTime.toFixed(2)}s
    </div>
    const wrapperClass = 'absolute parent flex justify-between'

    switch (props.noteProp.Type) {
        case 3:
        case 5:
        case 4: return (<div style={lineWrapper} className={wrapperClass}><button onClick={() => { console.log(props.noteProp.JudgeTime) }} style={lineStyle}></button>{noteInform}</div>)
        case 2: return (<div style={endWrapper} className={wrapperClass}><button onClick={() => { console.log(props.noteProp.JudgeTime) }} className='z-10 border-t border-black' style={endStyle}></button>{noteInform}</div>)
        default: return (<div style={singleWrapper} className={wrapperClass}><button onClick={() => { console.log(props.noteProp.JudgeTime) }} className='z-10 border border-black rounded-full' style={singleStyle}></button>{noteInform}</div>);
    }
}

export default Note;