import CastColor from "./CastColor";

function CastSetting(props: any) {
    const updateColors = (newcolors: any) => {
        props.setChartColors(newcolors);
    }
    return (<div className="sticky self-start top-0 left-0 backdrop-blur-sm z-40 bg-white/75">
        <CastColor colors={props.colors} updateColors={updateColors}/>
    </div>
    )
}

export default CastSetting;