import React from 'react';
import Songs from './pages/Songs';
import Chart from './pages/Chart';
import { Route, Routes } from 'react-router-dom';
import pageTop from './down_arrow.svg';
import './App.css';

function App() {
  const scrollDown = () => {
      window.scrollTo({
        top: document.documentElement.scrollHeight,
        behavior: 'smooth',
      });
  };
  const scrollUp = () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
  };

  return (
    <div className="App flex justify-center my-4">
      <Routes>
        <Route path='/' element={<Songs />}></Route>
        <Route path='/Chart' element={<Chart />}></Route>
      </Routes>
      <div className='fixed right-4 bottom-4 h-8 w-8'><button onClick={scrollDown}><img src={pageTop} height='2rem' alt='Go to page bottom'/></button></div>
      <div className='fixed right-4 bottom-16 h-8 w-8 rotate-180'><button onClick={scrollUp}><img src={pageTop} height='2rem' alt='Go to page top'/></button></div>
    </div>
  );
}

export default App;
