import { useState } from 'react'
import { ChromePicker } from 'react-color'
function CastColor(props: any) {
    const [pickerState, setPickerState] = useState([false, false, false, false, false]);
    const [colors, setColors] = useState(["rgb(255, 153, 200)", "rgb(252, 246, 189)", "rgb(208, 244, 222)", "rgb(169, 222, 249)", "rgb(228, 193, 249)"]);
    const colorClassName = 'w-5 h-5 rounded'
    const panelClassName = "flex w-16"
    const pickerWrapperName = 'absolute top-9'

    const handleDisplayPicker = (index: number) => {
        setPickerState([
            ...pickerState.slice(0, index),
            !pickerState[index],
            ...pickerState.slice(index + 1)]
        )
    }

    const handleColorChange = (color: any, index:number) => {
        let tmpColor = 'rgb('+color.rgb.r+','+color.rgb.g+','+color.rgb.b+')';
        let newcolors = [
            ...colors.slice(0, index),
            tmpColor,
            ...colors.slice(index+1)
        ];
        setColors(newcolors);
        props.updateColors(newcolors)
    };

    return (<div className="flex pt-4 pl-12">
        <div className={panelClassName}>
            <button className={colorClassName} style={{ backgroundColor: colors[3] }} onClick={() => handleDisplayPicker(3)} />4th
        </div>
        {pickerState[3] &&
            <div className={pickerWrapperName}>
                <ChromePicker disableAlpha={true} color={colors[3]} onChangeComplete={(color)=>handleColorChange(color,3)}/>
            </div>}
        <div className={panelClassName}>
            <button className={colorClassName} style={{ backgroundColor: colors[1] }} onClick={() => handleDisplayPicker(1)} />2nd
        </div>
        {pickerState[1] &&
            <div className={pickerWrapperName + ' left-20'}>
                <ChromePicker disableAlpha={true} color={colors[1]} onChangeComplete={(color)=>handleColorChange(color,1)}/>
            </div>}
        <div className={panelClassName}>
            <button className={colorClassName} style={{ backgroundColor: colors[0] }} onClick={() => handleDisplayPicker(0)} />1st
        </div>
        {pickerState[0] &&
            <div className={pickerWrapperName + ' left-24'}>
                <ChromePicker disableAlpha={true} color={colors[0]} onChangeComplete={(color)=>handleColorChange(color,0)}/>
            </div>}
        <div className={panelClassName}>
            <button className={colorClassName} style={{ backgroundColor: colors[2] }} onClick={() => handleDisplayPicker(2)} />3rd
        </div>
        {pickerState[2] &&
            <div className={pickerWrapperName + ' right-20'}>
                <ChromePicker disableAlpha={true} color={colors[2]} onChangeComplete={(color)=>handleColorChange(color,2)}/>
            </div>}
        <div className={panelClassName}>
            <button className={colorClassName} style={{ backgroundColor: colors[4] }} onClick={() => handleDisplayPicker(4)} />5th
        </div>
        {pickerState[4] &&
            <div className={pickerWrapperName + ' right-0'}>
                <ChromePicker disableAlpha={true} color={colors[4]} onChangeComplete={(color)=>handleColorChange(color,4)}/>
            </div>}
    </div>)
}

export default CastColor;